<template>
	  <div>
	  <nav-header></nav-header>
	  <nav-bread>
	  	<span>收货地址</span>
	  </nav-bread>
  		<div class="checkout-page">
  <svg style="position: absolute; width: 0; height: 0; overflow: hidden;" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
    <defs>
      <symbol id="icon-add" viewBox="0 0 31 32">
        <title>add</title>
        <path class="path1" d="M30.745 15.152h-14.382v-14.596c0-0.308-0.243-0.557-0.543-0.557s-0.543 0.249-0.543 0.557v14.596h-14.665c-0.3 0-0.543 0.249-0.543 0.557s0.243 0.557 0.543 0.557h14.665v15.177c0 0.307 0.243 0.557 0.543 0.557s0.543-0.249 0.543-0.557v-15.177h14.382c0.3 0 0.543-0.249 0.543-0.557s-0.243-0.557-0.543-0.557z"></path>
      </symbol>
      <symbol id="icon-ok" viewBox="0 0 32 32">
        <title>ok</title>
        <path class="path1" d="M14.084 20.656l-7.845-9.282c-1.288-1.482-3.534-1.639-5.016-0.351s-1.639 3.534-0.351 5.016l10.697 12.306c1.451 1.669 4.057 1.623 5.448-0.096l18.168-22.456c1.235-1.527 0.999-3.765-0.528-5.001s-3.765-0.999-5.001 0.528l-15.573 19.337z"></path>
      </symbol>
      <symbol id="icon-edit" viewBox="0 0 32 32">
        <title>edit</title>
        <path class="path1" d="M28.287 8.51l-4.805-4.806 0.831-0.831c0.472-0.472 1.086-0.777 1.564-0.777 0.248 0 0.452 0.082 0.622 0.253l3.143 3.144c0.539 0.54 0.133 1.529-0.524 2.186l-0.831 0.831zM26.805 9.992l-1.138 1.138-4.805-4.806 1.138-1.138 4.805 4.806zM24.186 12.612l-14.758 14.762-4.805-4.806 14.758-14.762 4.805 4.806zM7.379 28.288l-4.892 1.224 1.223-4.894 3.669 3.67zM31.123 4.011l-3.143-3.144c-0.567-0.567-1.294-0.867-2.103-0.867-1.036 0-2.174 0.52-3.045 1.391l-20.429 20.436c-0.135 0.134-0.23 0.302-0.276 0.487l-2.095 8.385c-0.089 0.355 0.017 0.736 0.276 0.995 0.198 0.198 0.461 0.307 0.741 0.307 0.085 0 0.171-0.010 0.254-0.031l8.381-2.096c0.185-0.047 0.354-0.142 0.487-0.276l20.43-20.436c1.409-1.41 2.042-3.632 0.524-5.15v0z"></path>
      </symbol>
      <symbol id="icon-del" viewBox="0 0 32 32">
        <title>delete</title>
        <path class="path1" d="M11.355 4.129v-2.065h9.29v2.065h-9.29zM6.194 29.935v-23.742h19.613v23.742h-19.613zM30.968 4.129h-8.258v-3.097c0-0.569-0.463-1.032-1.032-1.032h-11.355c-0.569 0-1.032 0.463-1.032 1.032v3.097h-8.258c-0.569 0-1.032 0.463-1.032 1.032s0.463 1.032 1.032 1.032h3.097v24.774c0 0.569 0.463 1.032 1.032 1.032h21.677c0.569 0 1.032-0.463 1.032-1.032v-24.774h3.097c0.569 0 1.032-0.463 1.032-1.032s-0.463-1.032-1.032-1.032v0z"></path>
        <path class="path2" d="M10.323 9.806c-0.569 0-1.032 0.463-1.032 1.032v14.452c0 0.569 0.463 1.032 1.032 1.032s1.032-0.463 1.032-1.032v-14.452c0-0.569-0.463-1.032-1.032-1.032z"></path>
        <path class="path3" d="M16 9.806c-0.569 0-1.032 0.463-1.032 1.032v14.452c0 0.569 0.463 1.032 1.032 1.032s1.032-0.463 1.032-1.032v-14.452c0-0.569-0.463-1.032-1.032-1.032z"></path>
        <path class="path4" d="M21.677 9.806c-0.569 0-1.032 0.463-1.032 1.032v14.452c0 0.569 0.463 1.032 1.032 1.032s1.032-0.463 1.032-1.032v-14.452c0-0.569-0.463-1.032-1.032-1.032z"></path>
      </symbol>
      <symbol id="icon-clock" viewBox="0 0 32 32">
        <title>clock</title>
        <path class="path1" d="M29.333 16c0-7.364-5.97-13.333-13.333-13.333s-13.333 5.97-13.333 13.333c0 7.364 5.97 13.333 13.333 13.333s13.333-5.97 13.333-13.333v0 0 0 0 0 0zM0 16c0-8.837 7.163-16 16-16s16 7.163 16 16c0 8.837-7.163 16-16 16s-16-7.163-16-16zM14.667 14.667v1.333h2.667v-10.667h-2.667v9.333zM24 18.667h1.333v-2.667h-10.667v2.667h9.333z"></path>
      </symbol>
      <symbol id="icon-question" viewBox="0 0 32 32">
        <title>question</title>
        <path class="path1" d="M16 2.56c7.411 0 13.44 6.029 13.44 13.44s-6.029 13.44-13.44 13.44c-7.411 0-13.44-6.029-13.44-13.44s6.029-13.44 13.44-13.44zM16 0c-8.822 0-16 7.178-16 16s7.178 16 16 16c8.822 0 16-7.178 16-16s-7.178-16-16-16z"></path>
        <path class="path2" d="M16 22.080c-1.059 0-1.92 0.861-1.92 1.92s0.861 1.92 1.92 1.92c1.059 0 1.92-0.861 1.92-1.92s-0.861-1.92-1.92-1.92z"></path>
        <path class="path3" d="M12.16 12.48c0.706 0 1.28-0.574 1.28-1.28 0-1.412 1.148-2.56 2.56-2.56s2.56 1.148 2.56 2.56c0 1.412-1.148 2.56-2.56 2.56-0.706 0-1.28 0.574-1.28 1.28v3.84c0 0.706 0.574 1.28 1.28 1.28s1.28-0.574 1.28-1.28v-2.723c2.224-0.575 3.84-2.616 3.84-4.957 0-2.823-2.297-5.12-5.12-5.12s-5.12 2.297-5.12 5.12c0 0.706 0.574 1.28 1.28 1.28z"></path>
      </symbol>
    </defs>
  </svg>
  <div class="container">
    <div class="checkout-addr">
      <div class="page-title-normal">
        <h2 class="page-title-h2"><span>收货地址</span></h2>
      </div>
      <!-- process step -->
      <div class="check-step">
        <ul>
          <li class="cur"><span>选择收货地址</span></li>
          <li><span>查看订单</span></li>
          <li><span>支付中</span></li>
          <li><span>下单成功</span></li>
        </ul>
      </div>

      <!-- address list -->
      <div class="page-title-normal checkout-title">
        <h2><span>收货地址</span></h2>
      </div>
      <div class="addr-list-wrap">
        <div class="addr-list">
          <ul>
            <li v-for="(item,index) in addressListFilter" :class="{'check':checkIndex==index}" @click="checkIndex=index;selectedAddreid=item.addressId" >
              <dl>
                <dt>{{item.userName}}</dt>
                <dd class="address">{{item.streetName}}</dd>
                <dd class="tel">{{item.tel}}</dd>
              </dl>
              <div class="addr-opration addr-del">
                <a href="javascript:;" class="addr-del-btn" @click='delAddressConfirm(item.addressId)'>
                  <svg class="icon icon-del"><use xlink:href="#icon-del"></use></svg>
                </a>
              </div>
              <div class="addr-opration addr-set-default">
                <a href="javascript:;" class="addr-set-default-btn" v-if='!item.isDefault' @click="setDefault(item.addressId)"><i>设置默认地址</i></a>
              </div>
              <div class="addr-opration addr-default" v-if='item.isDefault'>默认地址</div>
            </li>
            <li class="addr-new"@click="ressOff('ruleForm')">
              <div class="add-new-inner">
                <i class="icon-add">
                  <svg class="icon icon-add"><use xlink:href="#icon-add"></use></svg>
                </i>
                <p>添加新地址</p>
              </div>
            </li>
          </ul>
        </div>

        <div class="shipping-addr-more">
          <a class="addr-more-btn up-down-btn" href="javascript:;" @click="expand" :class="{'open':limit>3}">
            展开
            <i class="i-up-down">
              <i class="i-up-down-l"></i>
              <i class="i-up-down-r"></i>
            </i>
          </a>
        </div>
      </div>

      <!-- shipping method-->
      <div class="page-title-normal checkout-title">
        <h2><span>送货方式</span></h2>
      </div>
      <div class="shipping-method-wrap">
        <div class="shipping-method">
          <ul>
            <li class="check">
              <div class="name">顺丰快递</div>
              <div class="price">包邮</div>
              <div class="shipping-tips">
                <p>订单将在1-7天内到达目的地。</p>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div class="next-btn-wrap">
        <a class="btn btn--m btn--red" @click='btnNext'>下一步</a>
      </div>
    </div>
  </div>
</div>
	<modal :mdShow='isMdshow' @close="closeModal">
		<p slot="message">您是否确认删除</p>
		<div slot='btnGroup'>
			<a  class="btn btn--m" href="javascript:;" @click="delAddress">确认</a>
			<a  class="btn btn--m" href="javascript:;" @click="isMdshow=false">取消</a>
		</div>
	</modal>
       <div class="md-modal modal-msg md-modal-transition " v-bind:class="{'md-show':ress}">
          <div class="md-modal-inner">
            <div class="md-top">
              <div class="md-title">添加地址</div>
              <button class="md-close" @click='ress=false'>Close</button>
            </div>
            <div class="md-content">
              <div class="confirm-tips">
                    <div class="error-wrap">
                     <span class="error error-show"></span>
                </div>
                            <el-form   :model="ruleForm" status-icon :rules="rules2" ref="ruleForm"  class="demo-ruleForm">
                   <el-form-item label="" prop="userName" >
                    <el-input type="text" v-model="ruleForm.userName" :maxlength=namesize placeholder='收件人姓名'  auto-complete="off"></el-input>
                  </el-form-item>  
                  <el-form-item label="" prop="tel">
                    <el-input type="text" v-model="ruleForm.tel"  :maxlength=telsize placeholder='收件人手机'   auto-complete="off"></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="postCode">
                      <el-input type="text" v-model="ruleForm.postCode" :maxlength=postsize placeholder='邮政编号'  auto-complete="off"></el-input>
                  </el-form-item>
                  <el-form-item  label="" prop="streetName" >
                      <el-input type='text' placeholder='收件人地址' v-model="ruleForm.streetName"></el-input>
                  </el-form-item>
                </el-form>
              </div>
              <div class="login-wrap">
                <a href="javascript:;" class="btn-login" @click='addAddress("ruleForm")'>确定</a>
              </div>
            </div>
          </div>
        </div>
             <div class="md-overlay" v-if='ress' @click='ress=false'></div>
	<nav-footer></nav-footer>
     </div>
</template>
  <style scoped>
.el-form {
    position: relative;
    right: 0%;
}
.el-input{
  width: 100%;
}
@media only screen and (max-width: 767px){
.el-form-item__content {
    margin-left: 0px!important;
}
.item__content {
    /*margin-left: 0px!important;*/
}
}
  </style>

<script>
import NavHeader from '@/components/Header'
import NavFooter from '@/components/Footer'
import NavBread from '@/components/Bread'
import Modal from '@/components/Modal'
// import '../assets/css/checkout.css'
import axios from 'axios'
  export default{

        data(){
         var validatename = (rule, value, callback) => {
        if (value.trim()==='') {
          callback(new Error('请输入收件人姓名'));
        }else if(value.length<2){
           callback(new Error('姓名不能少于两个字符'));
        }else {
          callback();
        }
      };
      var validatetel = (rule, value, callback) => {
       let reg = /^(((13[0-9]{1})|(14[0-9]{1})|(17[0]{1})|(15[0-3]{1})|(15[5-9]{1})|(18[0-9]{1}))+\d{8})$/
        if (value.trim()==='') {
          callback(new Error('请输入手机号'));
        }else if(!reg.test(value)){
           callback(new Error('请输入正确手机号'));
        }else {
          callback();
        }
      };
      var validatepost = (rule, value, callback) => {
        if (value.trim()==='') {
          callback(new Error('请输入邮政编码'));
        } else if (value.length<6) {
          callback(new Error('邮政编码不能少于六位'));
        } else {
          callback();
        }
      };
      var validatestreet = (rule, value, callback) => {
         if (value.trim()==='') {
          callback(new Error('请输入地址'));
        } else if (value.length<10) {
          callback(new Error('地址不能少于十个字符'));
        } else {
          callback();
        }
      };
            return{
               time:true,
              namesize:10,
              telsize:11,
              postsize:10,
              ress:false,
            	limit:3,
            	checkIndex:0,
            	selectedAddreid:'',
            	addressList:[],
            	isMdshow:false,
            	addressId:'',
          ruleForm: {
              userName:'',
              streetName:'',
              postCode:'',
              tel:'',
        },
          rules2: {
          //注册
          userName: [
            { validator: validatename,trigger: 'blur,change' }
          ],
          tel: [
            { validator: validatetel, trigger: 'blur,change' }
          ],
          postCode: [
            { validator: validatepost, trigger: 'blur,change' }
          ],
          streetName: [
            { validator: validatestreet, trigger: 'blur,change' }
          ],
        }
            }
        },
         components: { 
          NavHeader,
          NavFooter,
          NavBread,
          Modal,
        },
         mounted(){
          this.init();
        },
        computed:{
        	addressListFilter(){
        		return this.addressList.slice(0,this.limit)
        	}
        },
        methods:{
        	init(){
        		axios.get('/users/addressList').then((response)=>{
        			let res=response.data;
        			if (res.status=='0'){
        				this.addressList=res.result.reverse();
                this.addressList.forEach((item,index) => {
                      if(item.isDefault){
                          this.selectedAddreid = item.addressId;
                      }
                  });
        			};
        		//	console.log(this.addressList)
        		})
        	},
        	expand(){
        		if(this.limit==3){
        			this.limit=this.addressList.length;
        		}else{
        			this.limit=3
        		}
        	},
        	setDefault(addressId){
        		axios.post('/users/setDefault',{addressId:addressId}).then((response)=>{
        			let res=response.data;
        			if (res.status=='0'){
        				this.init();
        			};
        		})
        	},
        	closeModal(){
        		this.isMdshow-false;
        	},
        	delAddressConfirm(addressId){
                this.isMdshow=true;
                this.addressId=addressId;
        	},
        	delAddress(){
        		axios.post('/users/delAddress',{addressId:this.addressId}).then((response)=>{
        			let res=response.data;
        			if (res.status=='0'){
        				this.isMdshow=false;
        				this.init();
        			};
        		})
        	},
          btnNext(){
              if (this.addressList.length==0){
                     this.$notify.info({
                        title: 'Tip',
                        message: '请您设置一个默认收货地址',
                        duration: 3000
                });
            }else if(this.selectedAddreid==''){
                this.$notify.info({
                        title: 'Tip',
                        message: '请点击选择一个地址',
                        duration: 3000
                });
            }else{
                this.$router.push({
                  path:"/orderConfirm?addressId="+this.selectedAddreid+"&shopid="+this.$route.query.shopid,
                })
            }
          },
          ressOff(formName){
            this.ress=true
              this.$refs[formName].resetFields();
          },
          addAddress(formName){
  console.log('1')
          if(!this.time){
             return false;
          }
            console.log('2')
          var _this=this
          this.time=false;            
          var time=setTimeout(function(){
             _this.time=true;
             clearInterval(time);  
          },2000); 
          this.$refs[formName].validate((valid) => {
            if (valid) {
            axios.post('/users/addAddress',{
                userName:this.ruleForm.userName,
                streetName:this.ruleForm.streetName,
                postCode:this.ruleForm.postCode,
                tel:this.ruleForm.tel
            }).then((response)=>{
            let res=response.data;
              if (res.status=='0'){
                 this.ress=false
                this.$notify({
                  title: 'Tip',
                  message: '添加地址成功',
                  type: 'success'
              });
                   this.init();
              };
            })
                } else {
               this.$notify.error({
                  title: '错误提示',
                  message: '您的输入有误请重新输入',
                  duration: 2000
                });
            return false;
          }
          })
                
          }
        }

    }
</script>


